@import url(https://fonts.googleapis.com/css2?family=Advent+Pro:wght@600&display=swap);
*{
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  font-family: 'Advent Pro', sans-serif;
  transition: .3s ease;
}


/* //colors and default size. */
:root{
  --primaryColor: #00eaff;
  --primaryHover: #00eaff;
  --secondaryColor: rgb(150, 162, 188);
  --paragraphColor: rgb(127, 127, 175);
  --BgColor: #031a30;
  --fromColor: rgb(2, 15, 29);
  --projectCardColor: #072340;
  --textcolor: #dae4fb;
  --headerHeight: 3rem;
}

/* Default Css setup */

html{
  scroll-behavior: smooth;
}

a{
  text-decoration: none;
}

li{
  list-style: none;
}

.flex{
  display:flex;
  align-content:center;
}

.grid{
  display: grid;
  align-content:center;
}

input, textarea{
  font-size: 100%;
}

.section{
  padding: 4rem 0 2rem;
}

.container{
  padding-left: 1.5rem;
  padding-right: 1.5rem;
}

img{
  height: auto;
  width: 100%;
}

.icon{
  font-size: 25px;
  cursor: pointer;
}

.btn{
  border: 2px solid #00eaff;
  border: 2px solid var(--primaryHover);
  padding: 0.5rem 1.5rem;
  font-size: 1.1rem;
  font-weight: 600;
  position: relative;
  color: #00eaff;
  color: var(--primaryColor);
  background: transparent;
}


/* button hover effect */
.btn::before{
  content: '';
  height: 100%;
  width: 0%;
  background: #00eaff;
  background: var(--primaryColor);
  position: absolute;
  top: 0;
  left: 0;
  z-index: -1;
  transition: .2s;
}
.btn::after{
  content: '';
  height: 100%;
  width: 0%;
  background: #031a30;
  background: var(--BgColor);
  position: absolute;
  top: 0;
  left: 0;
  z-index: -1;
  transition: .5s;
}

.btn:hover::before, .btn:hover:after{
  width: 100%;
}

.navLink{
  position: relative;
}


/* links hover effect */
.navLink::after{
  content: '';
  position: absolute;
  background: #00eaff;
  background: var(--primaryHover);
  height: 2px;
  width: 0%;
  bottom: -3px;
  left: 0;
  transition: 0.3s;
}
.navLink::before{
  content: '';
  position: absolute;
  background: #072340;
  background: var(--projectCardColor);
  height: 2px;
  width: 0%;
  bottom: -3px;
  left: 0;
  transition: 0.8s;
}

/* section title */
.sectionTitle{
  padding: 1rem 0;
  font-size: 2rem;
  color: #dae4fb;
  color: var(--textcolor);
  display: flex;
  align-content: center;
}

.titleNumber{
  color: #00eaff;
  color: var(--primaryColor);
  margin: 0 0.6rem;
  font-weight: 600;
}

.titleText{
  position: relative;
  width: -webkit-max-content;
  width: max-content;
}

.underline{
  position: absolute;
  width: 100%;
  height: 3px;
  top: 35px;
  left: 0;
  border-radius: 10px;
  background: rgb(150, 162, 188);
  background: var(--secondaryColor);
  overflow: hidden;
}

.underline span{
  top: 0;
  left: 10px;
  height: 100%;
  width: 10px;
  border-radius: 10px;
  position: absolute;
  background: #dae4fb;
  background: var(--textcolor);
  -webkit-animation: moveLine 2s linear infinite;
          animation: moveLine 2s linear infinite;
}

@-webkit-keyframes moveLine{
  0%{
    left: -20px;
  }

  100%{
    left: 100px;
  }

}

@keyframes moveLine{
  0%{
    left: -20px;
  }

  100%{
    left: 100px;
  }

}

/* Done with default styling */

/* Body Styling */
body{
  background: #031a30;
  background: var(--BgColor);
  color: white;
}

::selection{
  background-color: #00eaff;
  background-color: var(--primaryColor);
  color: white;
}

::-webkit-scrollbar{
  width: 10px;
  background-color: #072340;
  background-color: var(--projectCardColor);
}

::-webkit-scrollbar-thumb{
  background: #00eaff;
  background: var(--primaryHover);
  border-radius: 10px;
}


@media screen and (min-width: 769px) {
  .container{
    width: 70%;
    margin: auto;
  }
}


@media screen and (min-width: 960px) {
  .section{
    padding: 6rem 0 4rem;
  }
}


@media screen and (min-width: 1240px) {
  .section{
    padding: 6rem 0 4rem;
  }

  .container{
    width: 70%;
    margin: auto;
  }
}

@media screen and (min-width: 1440px) {
  body{
    zoom: 1;
  }
}

@media screen and (min-width: 2560px) {
  body{
    zoom: 1.7;
  }
}
